.profile {
    margin-top: 20px;
    width: 600px;
    height: 150px;
    background: #232323;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    transition: 0.5s;
    box-shadow: 0 0 10px #000;
}

.image {
    display: flex;
    height: 100%;
    padding: 20px;
    justify-content: center;
    align-items: center;
    width: 150px;
}

.avatar {
    width: 100%;
    border-radius: 50%;
    transition: 0.5s;
    border: 5px solid #f7e018;
}

.avatar:hover {
    transform: translateY(-10px);
}

.info {
    width: 100%;
    padding: 20px;
    text-align: center;
    border-left: 10px dotted #333;
}

@media screen and (max-width: 800px) {
    .profile {
        display: block;
        height: auto;
        width: 300px;
    }
    .info {
        border-top: 10px dotted #333;
        border-left: none;
    }
    .image {
        width: 100%;
    }
}

.info h2 {
    color: #fff;
    text-align: center;
}

.info span {
    color: #fff;
    text-align: center;
}

.social {
    display: flex;
    width: 100%;
    justify-content: center;
    flex-wrap: wrap;
}

.social a {
    text-decoration: none;
}

.social a {
    padding: 5px;
    margin: 10px;
    border-radius: 4px;
    color: #232323;
    font-weight: bold;
    background: #f7e018;
    transition: 0.5s;
}

.social a:hover {
    transform: translateY(-10px);
    cursor: pointer;
}

.social a:nth-child(1):hover {
    transform: translateY(-10px);
    background: dodgerblue;
    color: #fff;
}

.social a:nth-child(2):hover {
    transform: translateY(-10px);
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
    color: #fff;
}

.social a:nth-child(3):hover {
    transform: translateY(-10px);
    background: #0073b1;
    color: #fff;
}

.social a:nth-child(4):hover {
    transform: translateY(-10px);
    background: #24292e;
    box-shadow: 0 0 5px #FFF;
    color: #fff;
}
